<template>
  <section>
    <!--  -->
    <div class="mb-4">
      <h3 class="text-center mb-3"></h3>
      <div
        :class="allFilesReady ? 'alert-success' : 'alert-warning'"
        class="alert"
      >
        <p>{{ allFilesReady ? "" : "" }}</p>
        <p v-for="(label, key) in fileLabels" :key="key">
          {{ label }}
          {{ fileStatus[key] ? "" : "" }}
        </p>
      </div>
    </div>

    <!--  -->
    <div class="text-center mb-4">
      <base-button
        :disabled="downloading"
        class="mx-2"
        type="primary"
        @click="downloadFiles"
      >
        <i class="ni ni-cloud-download-95 mr-2"></i>
        {{ downloading ? "..." : "" }}
      </base-button>

      <base-button
        :disabled="uploading || !allFilesReady"
        class="mx-2"
        type="success"
        @click="uploadFiles"
      >
        <i class="ni ni-cloud-upload-96 mr-2"></i>
        {{ uploading ? "..." : "" }}
      </base-button>

      <base-button
        :disabled="analyzing"
        class="mx-2"
        type="info"
        @click="requestAnalysis"
      >
        <i class="ni ni-chart-bar-32 mr-2"></i>
        {{ analyzing ? "..." : "" }}
      </base-button>
    </div>

    <!--  -->
    <div v-if="resultVisible">
      <!--  -->
      <div ref="resultCard" class="result-section">
        <!--  -->
        <card class="mb-4" shadow>
          <div class="p-3">
            <h3></h3>
            <p v-if="inviteCode" class="mb-0">
              <span>{{ inviteCode }}</span
              >
            </p>
            <p v-else class="text-muted">...</p>
          </div>
        </card>

        <!--  -->
        <card class="mb-4" shadow>
          <div class="p-3">
            <h3></h3>
            <div
              v-for="(trait, index) in traits"
              :key="trait.key"
              :class="{ 'mb-0': index === traits.length - 1 }"
            >
              <base-progress
                :height="20"
                :label="trait.label"
                :type="trait.type"
                :value="personalityScores[trait.key]"
              >
              </base-progress>
            </div>
          </div>
        </card>

        <!--  -->
        <card class="mb-4" shadow>
          <div class="p-3">
            <h3></h3>
            <p>
              
            </p>
            <div
              v-for="(trait, index) in traits"
              :key="'desc-' + trait.key"
              :class="{ 'mb-0': index === traits.length - 1 }"
            >
              <h6 :style="{ color: trait.color }">
                {{ trait.label }}{{ personalityScores[trait.key] }}%
              </h6>
              <p>
                {{ trait.description }}
              </p>
            </div>
          </div>
        </card>
      </div>

      <!--  -->
      <div class="text-center mt-4">
        <base-button
          :disabled="savingImage"
          class="mx-2"
          type="primary"
          @click="saveAsImage"
        >
          <i class="ni ni-camera-compact mr-2"></i>
          {{ savingImage ? "..." : "" }}
        </base-button>

        <router-link to="/">
          <base-button size="md" type="default" @click="handleLogout">
            <i class="ni ni-user-run mr-2"></i>
            
          </base-button>
        </router-link>
      </div>
    </div>
  </section>
</template>

<script>
import { mapActions } from "vuex";
import api from "@/services/api";
import ossService from "@/services/oss";

export default {
  name: "Result",
  props: {
    signatureFile: {
      type: File,
      required: true,
    },
    videoFile: {
      type: File,
      required: true,
    },
    videoTimesFile: {
      type: File,
      required: true,
    },
    answerFile: {
      type: File,
      required: true,
    },
  },
  data() {
    return {
      resultVisible: false,
      downloading: false,
      uploading: false,
      analyzing: false,
      fileStatus: {
        signature: false,
        video: false,
        videoTimes: false,
        answer: false,
      },
      fileLabels: {
        signature: "",
        video: "",
        videoTimes: "",
        answer: "",
      },
      personalityScores: {
        agreeableness: 0,
        openness: 0,
        extraversion: 0,
        conscientiousness: 0,
        neuroticism: 0,
      },
      traits: [
        {
          key: "openness",
          label: "",
          type: "default",
          color: "#172b4d",
          description:
            "",
        },
        {
          key: "conscientiousness",
          label: "",
          type: "success",
          color: "#2dce89",
          description:
            "",
        },
        {
          key: "extraversion",
          label: "",
          type: "warning",
          color: "#fb6340",
          description:
            "",
        },
        {
          key: "agreeableness",
          label: "",
          type: "primary",
          color: "#5e72e4",
          description:
            "",
        },
        {
          key: "neuroticism",
          label: "",
          type: "danger",
          color: "#f5365c",
          description:
            "",
        },
      ],
      savingImage: false,
      inviteCode: "",
    };
  },
  computed: {
    allFilesReady() {
      return Object.values(this.fileStatus).every((status) => status);
    },
  },
  methods: {
    ...mapActions("modal", ["showError", "showMessage"]),
    checkFiles() {
      console.log({
        signatureFile: this.signatureFile,
        videoFile: this.videoFile,
        videoTimesFile: this.videoTimesFile,
        answerFile: this.answerFile,
      });
      this.fileStatus = {
        signature: !!this.signatureFile,
        video: !!this.videoFile,
        videoTimes: !!this.videoTimesFile,
        answer: !!this.answerFile,
      };
    },

    async downloadFiles() {
      this.downloading = true;
      try {
        const fileConfigs = [
          { file: this.videoFile, filename: "video.webm" },
          { file: this.videoTimesFile, filename: "videoTimes.csv" },
          { file: this.signatureFile, filename: "signature.png" },
          { file: this.answerFile, filename: "answer.csv" },
        ];

        for (const config of fileConfigs) {
          if (config.file) {
            const url = URL.createObjectURL(config.file);
            const link = document.createElement("a");
            link.href = url;
            link.download = config.filename;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
            URL.revokeObjectURL(url);
          }
        }
        this.showMessage({
          title: "",
          message: "",
        });
      } catch (error) {
        this.showError({
          title: "",
          message: "",
        });
      } finally {
        this.downloading = false;
      }
    },

    async uploadFiles() {
      this.uploading = true;
      try {
        if (!this.signatureFile) {
          await this.showError({
            title: "",
            message: "",
          });
          return;
        }
        await ossService.uploadToOSS(
          "signature/part2",
          this.signatureFile,
          "signature",
        );

        if (!this.videoFile) {
          await this.showError({
            title: "",
            message: "",
          });
          return;
        }
        await ossService.multipartUploadToOSS(
          "video/part2",
          this.videoFile,
          "video",
        );

        if (!this.videoTimesFile) {
          await this.showError({
            title: "",
            message: "",
          });
          return;
        }
        await ossService.uploadToOSS(
          "videosTimes/part2",
          this.videoTimesFile,
          "videosTimes",
        );

        if (!this.answerFile) {
          await this.showError({
            title: "",
            message: "",
          });
          return;
        }
        await ossService.uploadToOSS("answer/part2", this.answerFile, "answer");

        this.showMessage({
          title: "",
          message: "",
        });
      } catch (error) {
        this.showError({
          title: "",
          message: "",
        });
      } finally {
        this.uploading = false;
      }
    },
    async requestAnalysis() {
      this.analyzing = true;
      try {
        if (!this.answerFile) {
          await this.showError({
            title: "",
            message: "",
          });
          return;
        }
        const answerFileFormData = new FormData();
        answerFileFormData.append("answer", this.answerFile);
        const response = await api.collection.getScore(answerFileFormData);
        if (
          response.status === 200 &&
          response.data &&
          response.data.code === 1
        ) {
          this.personalityScores = {
            agreeableness:
              Math.round(response.data.data.agreeableness * 10000) / 100 || 0,
            openness:
              Math.round(response.data.data.openness * 10000) / 100 || 0,
            extraversion:
              Math.round(response.data.data.extraversion * 10000) / 100 || 0,
            conscientiousness:
              Math.round(response.data.data.conscientiousness * 10000) / 100 ||
              0,
            neuroticism:
              Math.round(response.data.data.neuroticism * 10000) / 100 || 0,
          };
          this.resultVisible = true;
        } else {
          await this.showError({
            title: "",
            message: "",
          });
        }
      } catch (error) {
        await this.showError({
          title: "",
          message: "",
        });
      } finally {
        this.analyzing = false;
      }
    },
    handleLogout() {
      this.$store.commit("CLEAR_TOKEN");
      this.$router.push("/");
      this.showMessage({
        title: "",
        message: "",
      });
    },
    async saveAsImage() {
      this.savingImage = true;
      try {
        const html2canvas = (await import("html2canvas")).default;

        const element = this.$refs.resultCard;
        const canvas = await html2canvas(element, {
          backgroundColor: "#ffffff",
          scale: 2,
          useCORS: true,
          logging: false,
        });

        const link = document.createElement("a");
        link.download = "result.png";
        link.href = canvas.toDataURL("image/png");
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);

        this.showMessage({
          title: "",
          message: "",
        });
      } catch (error) {
        console.log("", error);
        this.showError({
          title: "",
          message: "",
        });
      } finally {
        this.savingImage = false;
      }
    },
    async getInviteCode() {
      try {
        const response = await api.auth.profile();
        if (
          response.status === 200 &&
          response.data &&
          response.data.code === 1
        ) {
          this.inviteCode = response.data.data.inviteCode;
        } else {
          this.showError({
            title: "",
            message: "",
          });
        }
      } catch (error) {
        console.log(error);
        this.showError({
          title: "",
          message: "",
        });
      }
    },
  },
  created() {
    this.checkFiles();
    this.getInviteCode();
  },
};
</script>

<style scoped>
h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-weight: 700 !important;
}
</style>
