<template>
  <section class="section section-shaped section-lg my-0">
    <div class="shape shape-style-1 bg-gradient-default">
      <span v-for="n in 8" :key="n"></span>
    </div>
    <div class="container pt-lg-md">
      <div class="row justify-content-center">
        <div class="col-lg-12">
          <card
            body-classes="px-lg-5 py-lg-5"
            class="border-0"
            header-classes="bg-white pb-5"
            shadow
            type="secondary"
          >
            <div class="text-center text-muted mb-4">
              <small></small>
            </div>
            <form role="form" @submit.prevent="handleSubmit">
              <div class="row">
                <!--  -->
                <div class="col-lg-4">
                  <base-input
                    v-model="formData.username"
                    addon-left-icon="ni ni-hat-3"
                    placeholder=""
                  >
                  </base-input>

                  <base-input
                    v-model="formData.fullname"
                    addon-left-icon="ni ni-single-02"
                    placeholder=""
                  >
                  </base-input>

                  <base-input
                    v-model="formData.studentId"
                    addon-left-icon="ni ni-badge"
                    placeholder=""
                  >
                  </base-input>

                  <base-input
                    v-model="formData.classCode"
                    addon-left-icon="ni ni-building"
                    placeholder=""
                  >
                  </base-input>

                  <base-input
                    v-model="formData.qq"
                    addon-left-icon="ni ni-chat-round"
                    placeholder="QQ"
                  >
                  </base-input>

                  <base-input
                    v-model="formData.phone"
                    addon-left-icon="ni ni-mobile-button"
                    placeholder=""
                  >
                  </base-input>

                  <base-input
                    v-model="formData.password"
                    addon-left-icon="ni ni-lock-circle-open"
                    placeholder=""
                    type="password"
                  >
                  </base-input>

                  <base-input
                    v-model="formData.confirmPassword"
                    addon-left-icon="ni ni-lock-circle-open"
                    placeholder=""
                    type="password"
                  >
                  </base-input>

                  <div class="form-group">
                    <div class="input-group">
                      <div class="input-group-prepend">
                        <span class="input-group-text"
                          ><i class="ni ni-calendar-grid-58"></i
                        ></span>
                      </div>
                      <flat-picker
                        v-model="formData.birthDate"
                        :config="datePickerConfig"
                        class="form-control"
                        placeholder=""
                      >
                      </flat-picker>
                    </div>
                  </div>

                  <base-input
                    v-model="formData.inviteCode"
                    addon-left-icon="ni ni-atom"
                    placeholder=""
                  >
                  </base-input>
                </div>

                <!--  -->
                <div class="col-lg-4">
                  <div class="form-group">
                    <label class="form-control-label"></label>
                    <div class="row">
                      <div class="col-6">
                        <div class="custom-control custom-radio mb-2">
                          <input
                            id="male"
                            v-model="formData.gender"
                            class="custom-control-input"
                            type="radio"
                            value="male"
                          />
                          <label class="custom-control-label" for="male"
                            ></label
                          >
                        </div>
                      </div>
                      <div class="col-6">
                        <div class="custom-control custom-radio mb-2">
                          <input
                            id="female"
                            v-model="formData.gender"
                            class="custom-control-input"
                            type="radio"
                            value="female"
                          />
                          <label class="custom-control-label" for="female"
                            ></label
                          >
                        </div>
                      </div>
                    </div>
                  </div>

                  <div class="form-group">
                    <label class="form-control-label"></label>
                    <div
                      v-for="career in careers"
                      :key="career.value"
                      class="custom-control custom-radio mb-2"
                    >
                      <input
                        :id="career.value"
                        v-model="formData.career"
                        :value="career.value"
                        class="custom-control-input"
                        type="radio"
                      />
                      <label :for="career.value" class="custom-control-label">{{
                        career.label
                      }}</label>
                    </div>
                  </div>
                </div>

                <!--  -->
                <div class="col-lg-4">
                  <div class="form-group">
                    <label class="form-control-label"></label>
                    <div class="row">
                      <div
                        v-for="level in educationLevels"
                        :key="level.value"
                        class="col-6 mb-2"
                      >
                        <div class="custom-control custom-radio">
                          <input
                            :id="level.value"
                            v-model="formData.educationLevel"
                            :value="level.value"
                            class="custom-control-input"
                            type="radio"
                          />
                          <label
                            :for="level.value"
                            class="custom-control-label"
                            >{{ level.label }}</label
                          >
                        </div>
                      </div>
                    </div>
                  </div>

                  <div class="form-group">
                    <label class="form-control-label"></label>
                    <div class="row">
                      <div
                        v-for="hobby in hobbies"
                        :key="hobby.value"
                        class="col-4 mb-2"
                      >
                        <div class="custom-control custom-checkbox">
                          <input
                            :id="hobby.value"
                            v-model="formData.hobbies"
                            :value="hobby.value"
                            class="custom-control-input"
                            type="checkbox"
                          />
                          <label
                            :for="hobby.value"
                            class="custom-control-label"
                            >{{ hobby.label }}</label
                          >
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>

              <div class="text-center">
                <base-button
                  :disabled="isSubmitting"
                  :loading="isSubmitting"
                  class="my-4"
                  type="primary"
                  @click="handleSubmit"
                >
                  {{ isSubmitting ? "..." : "" }}
                </base-button>
              </div>
            </form>
          </card>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { validationMixin } from "vuelidate";
import { minLength, required } from "vuelidate/lib/validators";
import flatPicker from "vue-flatpickr-component";
import "flatpickr/dist/flatpickr.css";
import api from "@/services/api";
import { mapActions } from "vuex";

export default {
  name: "Register",
  components: {
    flatPicker,
  },
  mixins: [validationMixin],
  data() {
    return {
      formData: {
        username: "",
        fullname: "",
        studentId: "",
        classCode: "",
        qq: "",
        phone: "",
        password: "",
        confirmPassword: "",
        birthDate: "",
        gender: "",
        educationLevel: "",
        career: "",
        hobbies: [],
        inviteCode: "",
      },
      isSubmitting: false,
      datePickerConfig: {
        allowInput: true,
        dateFormat: "Y-m-d",
      },
      educationLevels: [
        { value: "0", label: "" },
        { value: "1", label: "" },
        { value: "2", label: "" },
        { value: "3", label: "" },
      ],
      careers: [
        {
          value: "//",
          label: "//",
        },
        {
          value: "//",
          label: "//",
        },
        {
          value: "///",
          label: "///",
        },
        {
          value: "//",
          label: "//",
        },
        { value: "", label: "" },
        { value: "/", label: "/" },
        { value: "/", label: "/" },
        {
          value: "//",
          label: "//",
        },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "//", label: "//" },
        { value: "/", label: "/" },
        { value: "", label: "" },
      ],
      hobbies: [
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "DIY", label: "DIY" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
        { value: "", label: "" },
      ],
    };
  },
  validations: {
    formData: {
      username: { required, minLength: minLength(3) },
      fullname: { required },
      password: { required, minLength: minLength(6) },
      confirmPassword: {
        required,
        sameAsPassword(value) {
          return value === this.formData.password;
        },
      },
      studentId: {},
      classCode: {},
      qq: {
        required,
        numeric(value) {
          return /^\d+$/.test(value);
        },
      },
      phone: { required },
      birthDate: { required },
      gender: { required },
      educationLevel: { required },
      career: { required },
      hobbies: {
        required,
        minLength: minLength(1),
      },
    },
  },
  methods: {
    ...mapActions("modal", ["showMessage", "showError"]),
    validateForm() {
      this.$v.$touch();
      const errors = [];

      if (!this.formData.username) {
        errors.push("");
      } else if (this.formData.username.length < 3) {
        errors.push("3");
      }

      if (!this.formData.fullname) {
        errors.push("");
      }

      if (!this.formData.password) {
        errors.push("");
      } else if (this.formData.password.length < 6) {
        errors.push("6");
      }

      if (!this.formData.confirmPassword) {
        errors.push("");
      } else if (this.formData.confirmPassword !== this.formData.password) {
        errors.push("");
      }

      if (!this.formData.studentId) {
        errors.push("");
      }

      if (!this.formData.classCode) {
        errors.push("");
      }

      if (!this.formData.qq) {
        errors.push("QQ");
      } else if (!/^\d+$/.test(this.formData.qq)) {
        errors.push("QQ");
      }

      if (!this.formData.phone) {
        errors.push("");
      }

      if (!this.formData.birthDate) {
        errors.push("");
      }

      if (!this.formData.gender) {
        errors.push("");
      }

      if (!this.formData.educationLevel) {
        errors.push("");
      }

      if (!this.formData.career) {
        errors.push("");
      }

      if (!this.formData.hobbies || this.formData.hobbies.length === 0) {
        errors.push("");
      }

      return errors;
    },
    async handleSubmit() {
      const errors = this.validateForm();
      if (errors.length > 0) {
        this.showError({
          title: "",
          message: errors[0],
        });
        return;
      }

      this.isSubmitting = true;
      try {
        const response = await api.auth.register({
          username: this.formData.username,
          fullname: this.formData.fullname,
          password: this.formData.password,
          studentId: this.formData.studentId,
          classCode: this.formData.classCode,
          qq: this.formData.qq,
          phone: this.formData.phone,
          birthDate: this.formData.birthDate,
          gender: this.formData.gender,
          educationLevel: this.formData.educationLevel,
          career: this.formData.career,
          hobbies: this.formData.hobbies,
          inviteCode: this.formData.inviteCode,
        });
        if (
          response.status === 200 &&
          response.data &&
          response.data.code === 1
        ) {
          this.showMessage({
            title: "",
            message: "",
            onClose: () => {
              const redirectPath = this.$route.query.redirect || "/login";
              this.$router.replace(redirectPath);
            },
          });
        } else {
          this.showError({
            title: "",
            message: "",
          });
        }
      } catch (error) {
        console.log("", error);
        this.showError({
          title: "",
          message: "",
        });
      } finally {
        this.isSubmitting = false;
      }
    },
  },
};
</script>
