<template>
  <section>
    <h1 class="text-center mb-3"></h1>

    <!--  -->
    <div>
      <h3 class="mb-3"></h3>
      <div class="mb-3">
        <p></p>
        <p></p>
        <p></p>
      </div>
      <div class="d-flex align-items-center mb-3">
        <base-button
          :disabled="isRecordingAudio"
          type="primary"
          @click="startAudioRecording"
        >
          {{ isRecordingAudio ? "..." : "" }}
        </base-button>
        <base-button
          :disabled="!isRecordingAudio"
          class="ml-3"
          type="info"
          @click="stopAudioRecording"
        >
          
        </base-button>
        <base-button
          :disabled="!audioUrl"
          class="ml-3"
          type="success"
          @click="replayAudio"
        >
          
        </base-button>
      </div>
      <audio v-if="audioUrl" ref="audioPlayer"></audio>
      <div
        v-if="audioStatus"
        :class="[
          'alert',
          audioStatus.success ? 'alert-success' : 'alert-danger',
        ]"
      >
        {{ audioStatus.message }}
      </div>
    </div>

    <!--  -->
    <div>
      <h3 class="mb-3"></h3>
      <div class="mb-3">
        <p></p>
        <p></p>
        <p></p>
      </div>
      <div class="d-flex align-items-center mb-3">
        <base-button
          :disabled="isRecordingVideo"
          type="primary"
          @click="startVideoRecording"
        >
          {{ isRecordingVideo ? "..." : "" }}
        </base-button>
        <base-button
          :disabled="!isRecordingVideo"
          class="ml-3"
          type="info"
          @click="stopVideoRecording"
        >
          
        </base-button>
        <base-button
          :disabled="!videoUrl"
          class="ml-3"
          type="success"
          @click="replayVideo"
        >
          
        </base-button>
      </div>
      <div class="video-container mt-3 mb-3">
        <video
          v-if="isRecordingVideo || videoUrl"
          ref="videoElement"
          :autoplay="true"
          :controls="!!videoUrl"
          :muted="isRecordingVideo"
          class="video-preview"
          height="auto"
          playsinline
          width="100%"
        ></video>
      </div>
      <div
        v-if="videoStatus"
        :class="[
          'alert',
          videoStatus.success ? 'alert-success' : 'alert-danger',
        ]"
      >
        {{ videoStatus.message }}
      </div>
    </div>

    <!--  -->
    <div>
      <h3 class="mb-3"></h3>
      <div
        :class="allTestsPassed ? 'alert-success' : 'alert-warning'"
        class="alert"
      >
        <p>{{ allTestsPassed ? "" : "" }}</p>
        <p>
          :
          {{
            audioStatus ? (audioStatus.success ? "" : "") : ""
          }}
        </p>
        <p class="mb-0">
          :
          {{
            videoStatus ? (videoStatus.success ? "" : "") : ""
          }}
        </p>
      </div>
      <div class="text-center">
        <base-button
          :disabled="!allTestsPassed"
          type="primary"
          @click="startCollection"
        >
          
        </base-button>
      </div>
    </div>
  </section>
</template>

<script>
import RecordingService from "@/services/recordingService";

export default {
  name: "Test",
  data() {
    return {
      isRecordingAudio: false,
      isRecordingVideo: false,
      audioUrl: null,
      videoUrl: null,
      audioStatus: null,
      videoStatus: null,
      recordingService: new RecordingService(),
      recordingDuration: 5000,
    };
  },
  computed: {
    isDev() {
      return process.env.NODE_ENV === "development";
    },
    allTestsPassed() {
      return (
        this.isDev ||
        (this.audioStatus &&
          this.audioStatus.success &&
          this.videoStatus &&
          this.videoStatus.success)
      );
    },
  },
  methods: {
    async startAudioRecording() {
      try {
        this.isRecordingAudio = true;
        await this.recordingService.startAudioRecording(this.recordingDuration);
      } catch (error) {
        this.audioStatus = {
          success: false,
          message: "",
        };
        this.isRecordingAudio = false;
      }
    },

    async stopAudioRecording() {
      try {
        const result = await this.recordingService.stopRecording();
        this.audioUrl = result.url;
        this.audioStatus = {
          success: true,
          message: "",
          url: this.audioUrl,
        };
      } catch (error) {
        this.audioStatus = {
          success: false,
          message: "",
        };
      } finally {
        this.isRecordingAudio = false;
      }
    },

    async startVideoRecording() {
      try {
        this.isRecordingVideo = true;
        this.videoUrl = null;
        await this.$nextTick();
        await this.recordingService.startVideoRecording(
          this.$refs.videoElement,
          this.recordingDuration,
        );
      } catch (error) {
        this.videoStatus = {
          success: false,
          message: "",
        };
        this.isRecordingVideo = false;
      }
    },

    async stopVideoRecording() {
      try {
        const result = await this.recordingService.stopRecording(
          this.$refs.videoElement,
        );
        this.videoUrl = result.url;
        this.videoStatus = {
          success: true,
          message: "",
          url: this.videoUrl,
        };
      } catch (error) {
        this.videoStatus = {
          success: false,
          message: "",
        };
      } finally {
        this.isRecordingVideo = false;
      }
    },

    async replayAudio() {
      const audioPlayer = this.$refs.audioPlayer;
      if (audioPlayer && this.audioUrl) {
        audioPlayer.src = this.audioUrl;
        try {
          await audioPlayer.play();
        } catch (error) {
          this.audioStatus = {
            success: false,
            message: "",
          };
        }
      }
    },

    async replayVideo() {
      const videoElement = this.$refs.videoElement;
      if (videoElement && this.videoUrl) {
        videoElement.srcObject = null;
        videoElement.src = this.videoUrl;
        try {
          await videoElement.play();
        } catch (error) {
          this.videoStatus = {
            success: false,
            message: "",
          };
        }
      }
    },

    startCollection() {
      if (this.allTestsPassed) {
        this.$emit("finish");
      }
    },
  },
  beforeDestroy() {
    if (this.audioUrl) {
      URL.revokeObjectURL(this.audioUrl);
    }
    if (this.videoUrl) {
      URL.revokeObjectURL(this.videoUrl);
    }
  },
};
</script>

<style scoped>
h1,
h2,
h3,
h4,
h5,
p {
  font-weight: 700 !important;
}
</style>
