<template>
  <div class="profile-page">
    <section class="section-profile-cover section-shaped my-0">
      <div class="shape shape-style-1 shape-primary shape-skew alpha-4">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </section>
    <section class="section section-skew">
      <div class="container">
        <card class="card-profile mt--300" no-body shadow>
          <div class="px-4">
            <div class="row justify-content-center">
              <div class="col-lg-3 order-lg-2">
                <div class="card-profile-image">
                  <a href="#">
                    <img
                      :src="avatarUrlText"
                      alt="avatar"
                      class="rounded-circle"
                    />
                  </a>
                </div>
              </div>
              <div
                class="col-lg-4 order-lg-3 text-lg-right align-self-lg-center"
              >
                <div class="card-profile-actions py-4 mt-lg-0">
                  <base-button
                    class="mr-4"
                    size="sm"
                    type="info"
                    @click="copyInviteCode"
                  >
                    
                  </base-button>
                  <base-button
                    class="float-right"
                    size="sm"
                    type="default"
                    @click="handleLogout"
                  >
                    
                  </base-button>
                </div>
              </div>
              <div class="col-lg-4 order-lg-1">
                <div class="card-profile-stats d-flex justify-content-center">
                  <div>
                    <span class="heading">{{ roleText }}</span>
                    <span class="description"></span>
                  </div>
                  <div>
                    <span class="heading">{{ collectionStatusText }}</span>
                    <span class="description"></span>
                  </div>
                </div>
              </div>
            </div>
            <div class="text-center mt-5">
              <h3>
                {{ profile.fullname || "" }} /
                {{ profile.username || "" }}
              </h3>
              <div class="h5">
                <badge pill type="default"
                  >{{ profile.studentId || "" }}
                </badge>
                <badge pill type="primary"
                  >{{ profile.classCode || "" }}
                </badge>
                <badge pill type="info">QQ{{ profile.qq || "" }}</badge>
                <badge pill type="success"
                  >{{ profile.phone || "" }}
                </badge>
              </div>
              <div class="h5">
                <badge pill type="warning"
                  >: {{ profile.birthDate || "" }}
                </badge>
                <badge pill type="danger">: {{ genderText }}</badge>
                <badge pill type="success"
                  >: {{ educationLevelText }}
                </badge>
                <badge pill type="info"
                  >: {{ profile.career || "" }}
                </badge>
                <badge pill type="primary">: {{ hobbiesText }}</badge>
              </div>
            </div>
            <div class="mt-5 py-5 border-top text-center">
              <div class="row justify-content-center">
                <div class="col-lg-9">
                  : {{ profile.registerTime || "" }}
                </div>
              </div>
            </div>
          </div>
        </card>
      </div>
    </section>
  </div>
</template>

<script>
import { mapActions } from "vuex";
import api from "@/services/api";

export default {
  name: "Profile",
  data() {
    return {
      profile: {
        id: "",
        studentId: "",
        username: "",
        fullname: "",
        classCode: "",
        role: 0,
        qq: "",
        gender: 0,
        educationLevel: 0,
        birthDate: "",
        phone: "",
        avatarUrl: "",
        career: "",
        hobbies: "",
        isCollected: 0,
        inviterCode: "",
        inviteCode: "",
        registerTime: "",
      },
      roleMap: {
        0: "",
        1: "",
      },
      collectionStatusMap: {
        0: "",
        1: "",
      },
      genderMap: {
        male: "",
        female: "",
      },
      educationLevelMap: {
        0: "",
        1: "",
        2: "",
        3: "",
      },
    };
  },
  computed: {
    avatarUrlText() {
      return "avatar.jpg";
    },
    roleText() {
      return this.roleMap[this.profile.role] || "";
    },
    collectionStatusText() {
      return this.collectionStatusMap[this.profile.isCollected] || "";
    },
    genderText() {
      return this.genderMap[this.profile.gender] || "";
    },
    educationLevelText() {
      return this.educationLevelMap[this.profile.educationLevel] || "";
    },
    hobbiesText() {
      if (!this.profile.hobbies) {
        return "";
      }
      return this.profile.hobbies.split(",").join("");
    },
  },
  methods: {
    ...mapActions("modal", ["showMessage", "showError"]),

    async getProfile() {
      try {
        const response = await api.auth.profile();
        if (
          response.status === 200 &&
          response.data &&
          response.data.code === 1
        ) {
          this.profile = response.data.data;
        } else {
          this.showError({
            title: "",
            message: "",
          });
        }
      } catch (error) {
        console.error("", error);
        this.showError({
          title: "",
          message: "",
        });
      }
    },

    copyInviteCode() {
      if (this.profile.inviteCode) {
        navigator.clipboard.writeText(this.profile.inviteCode).then(() => {
          this.showMessage({
            title: "",
            message: "",
          });
        });
      } else {
        this.showError({
          title: "",
          message: "",
        });
      }
    },

    handleLogout() {
      this.$store.commit("CLEAR_TOKEN");
      this.$router.push("/");
      this.showMessage({
        title: "",
        message: "",
      });
    },
  },
  created() {
    this.getProfile();
  },
};
</script>
